.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.home_page {
  min-height: 800px;
  background-color: #fff;
  min-width: 1200px;
}
.home_page header {
  background: url('~@/assets/images/home/top_bg.png') no-repeat;
  height: 190px;
  background-color: #26bf80;
  padding: 60px 170px 0;
}
.home_page header .inner {
  color: #fff;
}
.home_page header .inner .menu_logo img {
  width: 140px;
  vertical-align: baseline;
}
.home_page header .inner .menu_logo .logo_name {
  margin-left: 12px;
  font-size: 20px;
  font-weight: bold;
}
.home_page header .inner .user_info {
  text-align: left;
}
.home_page header .inner .user_info .user_avatar {
  float: left;
  margin-right: 30px;
  cursor: pointer;
}
.home_page header .inner .user_info .user_avatar img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}
.home_page header .inner .user_info .user_name {
  margin-top: 10px;
  cursor: pointer;
  font-size: 18px;
  line-height: 30px;
}
.home_page header .inner .user_info_line {
  width: 0;
  float: left;
  margin: 10px 50px 0;
  height: 60px;
  border-left: 1px solid #3fcf93;
  border-right: 1px solid #13a468;
}
.home_page header .inner .date_and_logout {
  float: left;
  padding-top: 8px;
  font-size: 14px;
}
.home_page header .inner .date_and_logout .date_now {
  line-height: 30px;
}
.home_page header .inner .date_and_logout .logout {
  cursor: pointer;
}
.home_page header .inner .date_and_logout .logout span {
  line-height: 25px;
  margin-left: 12px;
}
.home_page header .inner .choose_term {
  width: 370px;
  height: 34px;
  padding: 0 17px 0 20px;
  background-color: #24b377;
  margin-top: 23px;
  border: 1px solid #13a468;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.home_page header .inner .choose_term .term_icon {
  margin-top: 4px;
  margin-right: 14px;
}
.home_page header .inner .choose_term .term_text {
  line-height: 32px;
  margin-right: 42px;
}
.home_page header .inner .choose_term .term_line {
  margin: 0;
  height: 32px;
}
.home_page header .inner .choose_term .menu_item_title {
  cursor: pointer;
  line-height: 32px;
  color: #fff;
  display: inline-block;
  width: 190px;
  height: 32px;
  padding-left: 14px;
}
.home_page header .inner .choose_term .menu_item_title .item_title {
  display: inline-block;
  max-width: 140px;
  margin-right: 10px;
}
.home_page footer {
  width: 100%;
  height: 180px;
  background: url('~@/assets/images/home/foot_bg.png') no-repeat;
  background-color: #26bf80;
  position: fixed;
  bottom: 0;
}
.home_page footer .inner {
  width: 800px;
  margin: 0 auto;
  text-align: left;
  padding: 78px 0 0 238px;
  line-height: 20px;
  color: #fff;
  font-size: 12px;
}
.home_page footer .inner a {
  color: #fff;
}
.home_page footer .inner a:hover {
  text-decoration: underline;
}
.home_page footer .after_line {
  position: absolute;
  background-color: #64d2a5;
  left: 0;
  top: 1px;
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #fff;
}
.home_page .home_page_content {
  position: relative;
}
.home_page .home_page_content .before_line {
  position: absolute;
  top: 1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #26bf80;
}
.home_page .home_page_content .home_page__content {
  width: 60%;
  width: 1200px;
  margin: 0 auto;
  position: relative;
  padding-top: 4%;
  min-height: 570px;
  z-index: 10;
}
.home_page .home_page_content .home_page__content .home_page__content_item {
  float: left;
  margin-bottom: 40px;
  text-align: center;
  cursor: pointer;
}
.home_page .home_page_content .home_page__content .home_page__content_item img {
  width: 117px;
  height: 117px;
}
.home_page .home_page_content .home_page__content .home_page__content_item p {
  height: 60px;
  line-height: 60px;
  font-size: 18px;
  color: #383838;
  font-weight: bold;
}
